
import React, { useEffect, useState, useRef } from "react";
import { getStore, ObjectKeys, filterArrAccounts } from "../../../stores/Utils";

import "./styles.css"



const SelectEfectivoAcc  = ({ handleClick, query, type }) => {

	
	const [list, setList] = useState([]);
	const [open, setOpen] = useState(false);
	const [show, setShow] = useState(false);

	useEffect(() => {
		handle_search(query)
    },[query, type]);


    

	const handle_search =  (v) => {
		setList(filterArrAccounts(getStore("account_list"),v))
		if(!show){
			setOpen(true)
			setTimeout(() => {
				setShow(true)
			}, 200);
		}

	}

	
    
    const selectItem =  (acc,subacc) => {
		handleClick && handleClick(acc,subacc);
		if(show){
			setShow(false)
			setTimeout(() => {
				setOpen(false)
			}, 200);
		}
	}

	

    return (
		<>
			{open?
			 <div className={"boxOpenContainer"} style={{opacity:show?1:0, maxWidth: "90%"}}>
			<div className="">
					{list && ObjectKeys(list)?.map(cid=>{
						return (
							<AccountItem 
								data={list[cid]}
								elmId={cid} 
								key={cid}
								selectItem={selectItem}
							/>
						)
					})}
				</div>
				</div>

			:null}
		</>
    )   
}

export default SelectEfectivoAcc;




const AccountItem  = ({ data,  selectItem }) => {
	
	let subAcc = getStore("sub_account_group")?.[data.accountId];
	
	

	return (
		<div className="item_acc">
			<div className="_dsplFlx account_item_box account_header"  onClick={()=>selectItem(data?.accountId, null)}>
				<div className="_dsplFlx" >
					<div className="code" >
						{data?.code} 
					</div>
					<div className="name">
						{data?. name}
					</div>
				</div>
			</div>
			{subAcc?.length && 
			<div >
				{ subAcc?.map(subacc=>{
					if(!subacc.subAccount){
						return null
					}
					return(
						<div 
							key={subacc.sub_accountId} 
							className="_dsplFlx account_item_box"  
							style={{marginLeft:"35px"}}
							onClick={()=>selectItem(data?.accountId, subacc.sub_accountId)}
						>
							<div className="_dsplFlx" >
								<div className="code" >
									{subacc.subAccount}
								</div>
								<div className="name">
									{subacc.subAccountName}
								</div>
							</div>
						</div>
					)
				})}
			</div>
			}
		</div>
	)
}



